.App {
  color: #606770;
  background: #F0F2F5;
  min-height: 100vh;
}

body {
  overflow: hidden;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Segoe Ui";
}

img {
  max-width: 100%;
}

svg path {
  transition: fill .1s ease-in-out;
}

h3,
h4 {
  display: block;
  font-weight: 500;
  font-size: 15px;
  line-height: 15px;
  margin-bottom: 20px;
  padding-bottom: 12px;
  text-transform: capitalize;
  width: 100%;
  color: #fff;
  position: relative;
}

h3::before ,
h4::before {
  content: "";
  height: 90%;
  left: -20px;
  position: absolute;
  top: -5px;
  width: 3px;
  background: #1877F2;
  color: #606770;
}

h4 {
  font-size: 14px;
  padding: 0 20px 15px;
}

h4::before {
  height: 15px;
  top: 25%;
  transform: translateY(-50%);
  left: 0;
}

.themeBorder {
  margin-bottom: 20px;
  border-radius: 6px;
  background: #FFFFFF none repeat scroll 0 0;
  box-shadow: 1px 1px 1px 1px  #DADDE1;
  min-height: 100px;
  padding-bottom: 20px;
  padding-top: 20px;
}

.themeBorderPad {
  padding: 20px;
}

.innerCenter {
  margin-left: auto;
  margin-right: auto;
  padding: 0 30px;
  flex-grow: 1;
}

.innerWrapper {
  margin-left: auto;
  margin-right: auto;
}

.contentCenter {
  max-width: 1300px;
  padding-left: 100px;
  padding-right: 100px;
  margin-left: auto;
  margin-right: auto;
}

.centralContent {
  padding-top: 90px;
}

.wrapperWithWidgets {
  max-width: 1250px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 75px;
  padding-right: 75px;
  display: flex;
  align-items: flex-start;
}
